import React, { Component } from 'react';

class CommentSection extends Component {
    constructor(props) {
        super(props);
        this.state = {
            comments: []
        }
    }

    componentDidMount() {
        this.setState({
            comments: this.props.comments
        });
    }

    render() {
        return (
            <div>
                {this.state.comments.map(comment => {
                    return ( 
                    <div>
                        <h5>{comment.user}</h5>
                        <h5>{comment.rating}</h5>
                        <p>{comment.text}</p>
                    </div>
                    )
                })}
            </div>
        );
    }
}

export default CommentSection;


