import React, { Component } from 'react';
import CommentSection from '../CommentSection/CommentSection';
import './OfferContainer.css';
import NewComment from '../NewComment/NewComment';

class OfferContainer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            offer: this.props.offer
        }
    }

    componentDidMount() {
        this.setState({
            offer: this.props.offer
        })
    }

    render() {
        return (
            <div className="OfferContainer">
                <div>
                    <h1>{this.state.offer.company.name}</h1>
                    <p>{this.state.offer.company.description}</p>
                    <ul className="Row OfferContainer__receive">
                        <li>You receive: {this.state.offer.you_receive}</li>
                        <li>They receive: {this.state.offer.they_receive}</li>
                    </ul>
                </div>
                    <h4>What's the catch? </h4> <p>{this.state.offer.conditions}</p>
                    <h3>Comments</h3>
                    <CommentSection comments={this.state.offer.comments} />
                    <NewComment />
            </div>
        );
    }
}

export default OfferContainer;


