import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import dummyData from './DummyData.js';
import OfferContainer from './Components/OfferContainer/OfferContainer.js';
import bootstrap from 'bootstrap';

class App extends Component {
  constructor() {
    super();
    this.state = {
      data: {
        users: [],
        offers: []
      }
    }
  }

  componentDidMount() {
    this.setState({
      data: dummyData
    });
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">grapevine</h1>
          <p>An app for refferals</p>          
        </header>
        <div>
          {this.state.data.offers.map(offer => {
            return <OfferContainer key={offer.company}offer={offer} />
          })}
        </div>
      </div>
    );
  }
}

export default App;



