import React, { Component } from 'react';

class NewComment extends Component {
    state = {
        user: '',
        rating: '',
        text: ''
    };

    handleChangeRating = this.handleChangeRating.bind(this);
    handleChangeText = this.handleChangeText.bind(this);
    handleChangeUser = this.handleChangeUser.bind(this);

    handleChangeUser(event) {
        this.setState({
            user: event.target.value
        });
    }

    handleChangeRating(event) {
        this.setState({
            rating: event.target.value
        });
    }

    handleChangeText(event) {
        this.setState({
            text: event.target.value
        });
    }

    handleSubmit(event) {
        console.log('I tried!');
    }
    render() {
        return (
            <div>
                <form onSubmit={ () => this.handleSubmit() }>
                    <input type="text" value={this.state.user} onChange={ this.handleChangeUser } />
                    <input type="text" value={this.state.rating} onChange={ this.handleChangeRating }></input>
                    <input type="text" value={this.state.text} onChange={ this.handleChangeText }></input>
                    <input type="submit" value="Submit" />
                </form>
            </div>
        );
    }
}

export default NewComment;


